* {
  margin: 0;
  padding: 0;
}
.app {
  background-color: #fafafa;
}
.app_headerImage {
  object-fit: contain;
  width: 130px;
}
.app_header {
  position: sticky;
  top: 0;
  padding: 20px;
  border-bottom: 1px solid lightgrey;
  background-color: white;
  display: flex;
  justify-content: space-between;
}
.app_loginContainer > button {
  margin-left: 10px;
}
.app_signUp {
  display: flex;
  text-align: center;
  flex-direction: column;
}
.app_posts {
  padding: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
